import React from 'react'
import ReactDOM from 'react-dom/client'
import { QueryClient, QueryClientProvider } from '@tanstack/react-query'
import App from './App.jsx'
import './index.css'

// Crear instancia de QueryClient
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
      retry: 1,
      staleTime: 5 * 60 * 1000, // 5 minutos
    },
  },
})

// Nota: La interceptacin de /auth/* se maneja en index.html (script inline)
// que se ejecuta ANTES de que React se cargue. Este archivo solo monta React.

// Filtrar errores de extensiones del navegador que no afectan la aplicacin
const originalError = window.console.error
window.console.error = (...args) => {
  const errorMessage = args.join(' ')
  
  // Filtrar errores comunes de extensiones del navegador
  if (
    errorMessage.includes('message channel closed') ||
    errorMessage.includes('asynchronous response') ||
    errorMessage.includes('A listener indicated an asynchronous response') ||
    errorMessage.includes('Extension context invalidated') ||
    errorMessage.includes('Receiving end does not exist') ||
    errorMessage.includes('message channel closed before a response was received')
  ) {
    // Ignorar estos errores silenciosamente
    return
  }
  
  // Mostrar otros errores normalmente
  originalError.apply(console, args)
}

// Manejar errores no capturados de promesas (registrar ANTES de que React se cargue)
window.addEventListener('unhandledrejection', (event) => {
  const errorMessage = event.reason?.message || String(event.reason || '')
  
  // Filtrar errores comunes de extensiones del navegador
  if (
    errorMessage.includes('message channel closed') ||
    errorMessage.includes('asynchronous response') ||
    errorMessage.includes('A listener indicated an asynchronous response') ||
    errorMessage.includes('Extension context invalidated') ||
    errorMessage.includes('Receiving end does not exist') ||
    errorMessage.includes('message channel closed before a response was received')
  ) {
    // Prevenir que el error se muestre en la consola
    event.preventDefault()
    event.stopPropagation()
    return false
  }
}, true) // Usar capture phase para interceptar antes

ReactDOM.createRoot(document.getElementById('root')).render(
  <React.StrictMode>
    <QueryClientProvider client={queryClient}>
      <App />
    </QueryClientProvider>
  </React.StrictMode>
)