// Cliente API base para todas las peticiones
// Usar ruta relativa para que pase por el proxy de Vite (configurado en vite.config.js)
// El proxy redirige /api/* a http://localhost:8081
const API_BASE_URL = import.meta.env.VITE_API_URL || '/api';

// Helper para obtener el token de autenticacin
const getAuthToken = () => {
  // Intentar obtener de cookie
  const cookies = document.cookie.split(';');
  for (let cookie of cookies) {
    const [name, value] = cookie.trim().split('=');
    if (name === 'auth_token' && value) {
      return value;
    }
  }
  return null;
};

export const api = {
  async get(endpoint, options = {}) {
    try {
      // Obtener token de autenticacin
      const authToken = getAuthToken();
      const headers = {
        // No incluir Content-Type en GET requests (no tienen body)
        ...options.headers,
      };
      
      // Agregar header Authorization si hay token
      if (authToken) {
        headers['Authorization'] = `Bearer ${authToken}`;
      }
      
      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        method: 'GET',
        headers: headers,
        credentials: 'include', // Incluir cookies
      });

      // Si recibimos 401, intentar verificar autenticacin y redirigir si es necesario
      if (response.status === 401) {
        // Evitar mltiples redirecciones
        if (window._redirectingToLogin) {
          throw new Error('Unauthorized - Sesin expirada')
        }
        
        window._redirectingToLogin = true
        
        // Verificar si hay una sesin activa
        try {
          const authCheck = await fetch(`${API_BASE_URL}/api/auth/check`, {
            credentials: 'include'
          })
          
          if (!authCheck.ok) {
            // Si la verificacin falla, redirigir a login
            console.warn(' Sesin expirada, redirigiendo a login...')
            setTimeout(() => {
              window.location.href = '/login'
            }, 500)
            throw new Error('Unauthorized - Sesin expirada')
          }
        } catch (authError) {
          // Si falla la verificacin, redirigir a login
          console.warn(' Error verificando autenticacin, redirigiendo a login...')
          setTimeout(() => {
            window.location.href = '/login'
          }, 500)
          throw new Error('Unauthorized - Sesin expirada')
        }
        
        // Si la verificacin es exitosa pero an recibimos 401, puede ser un problema de permisos
        console.warn(' Sesin vlida pero recibiendo 401 - puede ser un problema de permisos')
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      if (!response.ok) {
        // Intentar obtener el mensaje de error del body
        let errorMessage = `HTTP error! status: ${response.status}`
        let errorData = null
        try {
          const text = await response.text()
          if (text) {
            try {
              errorData = JSON.parse(text)
              if (errorData.message) {
                errorMessage = errorData.message
              } else if (errorData.error) {
                errorMessage = errorData.error
              }
            } catch (e) {
              // Si no es JSON, usar el texto como mensaje
              errorMessage = text || errorMessage
            }
          }
        } catch (e) {
          // Si no se puede leer el body, usar el mensaje por defecto
        }
        const error = new Error(errorMessage)
        error.status = response.status
        error.response = { 
          status: response.status,
          data: errorData
        }
        throw error
      }

      const data = await response.json();
      return data;
    } catch (error) {
      // Para endpoints que pueden fallar silenciosamente (como /sap/orderConsump/add),
      // solo loguear como warning en lugar de error
      if (endpoint.includes('/sap/orderConsump/add')) {
        console.warn(` El backend no pudo procesar ${endpoint} (esto es esperado por ahora):`, error.message);
      } else {
      console.error(`Error en GET ${endpoint}:`, error);
      }
      throw error;
    }
  },

  async post(endpoint, body, options = {}) {
    try {
      // Obtener token de autenticacin
      const authToken = getAuthToken();
      const headers = {
        'Content-Type': 'application/json',
        ...options.headers,
      };
      
      // Agregar header Authorization si hay token
      if (authToken) {
        headers['Authorization'] = `Bearer ${authToken}`;
      }
      
      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        method: 'POST',
        headers: headers,
        credentials: 'include',
        body: JSON.stringify(body),
      });

      // Si recibimos 401, verificar autenticacin y redirigir si es necesario
      if (response.status === 401) {
        // Evitar mltiples redirecciones
        if (window._redirectingToLogin) {
          throw new Error('Unauthorized - Sesin expirada')
        }
        
        window._redirectingToLogin = true
        
        try {
          const authCheck = await fetch(`${API_BASE_URL}/api/auth/check`, {
            credentials: 'include'
          })
          
          if (!authCheck.ok) {
            console.warn(' Sesin expirada, redirigiendo a login...')
            setTimeout(() => {
              window.location.href = '/login'
            }, 500)
            throw new Error('Unauthorized - Sesin expirada')
          }
        } catch (authError) {
          console.warn(' Error verificando autenticacin, redirigiendo a login...')
          setTimeout(() => {
            window.location.href = '/login'
          }, 500)
          throw new Error('Unauthorized - Sesin expirada')
        }
        
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error(`Error en POST ${endpoint}:`, error);
      throw error;
    }
  },

  async put(endpoint, body, options = {}) {
    try {
      // Obtener token de autenticacin
      const authToken = getAuthToken();
      const headers = {
        'Content-Type': 'application/json',
        ...options.headers,
      };
      
      // Agregar header Authorization si hay token
      if (authToken) {
        headers['Authorization'] = `Bearer ${authToken}`;
      }
      
      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        method: 'PUT',
        headers: headers,
        credentials: 'include',
        body: JSON.stringify(body),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error(`Error en PUT ${endpoint}:`, error);
      throw error;
    }
  },

  async delete(endpoint, options = {}) {
    try {
      // Obtener token de autenticacin
      const authToken = getAuthToken();
      const headers = {
        // No incluir Content-Type en DELETE requests (no tienen body)
        ...options.headers,
      };
      
      // Agregar header Authorization si hay token
      if (authToken) {
        headers['Authorization'] = `Bearer ${authToken}`;
      }
      
      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        method: 'DELETE',
        headers: headers,
        credentials: 'include',
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error(`Error en DELETE ${endpoint}:`, error);
      throw error;
    }
  },
};

export default api;






